.ondo_picture{
    background-color: #C4CAD8;
    width: 100%;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.picture1{
    min-width: 750px;
    width: 1000px;
    opacity: 0.8;
   
}
.mengban{
    background-color: rgba(0, 0, 0, 0.137);
    width: 100%;
    height: 588.23px;
    position: absolute;
    transition: 0.5s;
}
.mengban:hover{
    background-color: rgba(0, 0, 0, 0.32);
    transition: 0.5s;
}
.ondo_title{

position: absolute;
left: 10%;
text-align: left;
font-size: 50px;
color: white;
opacity: 0.9;
text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.800);
}

.title_button{
    position: absolute;
    right: 10%;
    bottom: 10%;
    color: white;
    border:1px solid #ffffff;
    padding: 20px 60px;
    font-size: 20px;
    font-weight: 600;
    border-radius: 100px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.500);
    transition: 0.2s;
}
.title_button:hover{
    border:none;
    background-color: #715eff;
    transition: 0.3s;
    box-shadow: 0px 0px 10px #715eff;
}